<template>
  <div class="phone">
    <div class="phone-l">
      <p></p>
      <div class="el-tree">
        <e-tree></e-tree>
      </div>
    </div>
    <div class="phone-r">
      <div class="nav-form">
        <el-form :inline="true" :model="form" class="demo-form-inline">
          <el-form-item label="">
            <el-input v-model="form.name" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="SIP">
            <el-input v-model="form.SIP" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="form.stats" placeholder="">
              <el-option
                  :label="item.label"
                  :value="item.value"
                  v-for="item in relayType"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
<!--            <el-button type="primary" @click="onSubmit"></el-button>-->
<!--            <el-button @click="onSubmit"></el-button>-->
          </el-form-item>
        </el-form>
        <el-button type="primary" @click="addForms(null, '')"
        >
        </el-button
        >
      </div>

      <el-table
          :row-class-name="tableRowClassName"
          :data="list"
          style="width: 100%"
          v-if="list.length !== 0"
      >
        <el-table-column prop="date" label="" width="50">
          <template scope="scope">
            {{ scope.$index + 1 }}
          </template>
        </el-table-column>
        <el-table-column prop="directoryName" label=""></el-table-column>
        <el-table-column prop="directoryNumber" label="" width="180">
        </el-table-column>
        <el-table-column prop="diaplan" label="" width="180">
        </el-table-column>
        <el-table-column prop="deptName" label="">

        </el-table-column>
        <el-table-column prop="groupName" label="">

        </el-table-column>
        <el-table-column prop="domain" label=""></el-table-column>
        <el-table-column prop="balance" label=""></el-table-column>
        <el-table-column prop="expire" label=""></el-table-column>
        <el-table-column prop="overdraft" label="">
          <template scope="scope">
            <div v-if="scope.row.billingType === 1"></div>
            <div v-else></div>
          </template>
        </el-table-column>
        <el-table-column prop="overdraft" label="">
          <template scope="scope">
            <div v-if="scope.row.bussiness == 0"></div>
            <div v-else-if="scope.row.bussiness == 1"></div>
            <div v-else-if="scope.row.bussiness == 2"></div>
            <div v-else></div>
          </template>
        </el-table-column>
        <el-table-column prop="address" label="">
          <template scope="scope">
            <el-link
                class="a-link"
                type="info"
                @click="addForms(scope.row, '')"
            >
            </el-link
            >
            <el-link class="a-link" type="info" @click="removeIt(scope.row)"
            >
            </el-link
            >
            <el-link class="a-link" type="info" @click="recharge(scope.row)"
            >
            </el-link
            >
          </template>
        </el-table-column>
      </el-table>
      <my-empty v-else/>

    </div>

    <el-dialog :title="title" :visible.sync="dialogFormVisible">
      <el-form :model="addForm" :rules="rules" ref="addForm">
        <div class="width">
          <el-form-item

              label=""
              :label-width="formLabelWidth"
              prop="directoryName"
          >
            <el-input
                v-model="addForm.directoryName"
                autocomplete="off"
            >
            </el-input>
          </el-form-item>
          <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="accountUser"
          >
            <el-input
                v-model="addForm.accountUser"
                autocomplete="off"
            >
            </el-input>
          </el-form-item>

        </div>

        <div class="width">
          <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="billingType"
          >
            <el-select v-model="addForm.billingType" >
              <el-option
                  v-for="item in billingType"
                  :key="item.id"
                  :label="item.label"
                  :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="bussiness"
          >
            <el-select v-model="addForm.bussiness" >
              <el-option :label="item.label" :value="item.value" v-for="item in bussiness"> </el-option>
            </el-select>
          </el-form-item>
        </div>
        <div class="width">
          <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="directoryNumber"
          >
            <el-input
                v-model="addForm.directoryNumber"
                autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="password"
          >
            <el-input
                v-model="addForm.password"
                clearable
            >
            </el-input>
          </el-form-item>
        </div>
        <div class="width">
          <el-form-item label="" :label-width="formLabelWidth" prop="balance">
            <el-input v-model="addForm.balance" ></el-input>
          </el-form-item>

          <el-form-item label="" :label-width="formLabelWidth" prop="domain">
            <el-input v-model="addForm.domain" ></el-input>
          </el-form-item>
        </div>
        <div class="width">
          <el-form-item label="" :label-width="formLabelWidth" prop="disable">
            <el-select v-model="addForm.disable" placeholder="">
              <el-option
                  v-for="item in disable"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth" prop="deptId">
            <el-cascader
                v-model="deptId"
                :options="getterDeptIdList"
                :props="defaultProps"></el-cascader>
          </el-form-item>
        </div>
        <div class="width">
          <el-form-item label="" :label-width="formLabelWidth" prop="diaplan">
            <el-select v-model="addForm.diaplan" placeholder="">
              <el-option
                  v-for="item in diaPlanList"
                  :key="item.value"
                  :label="item.diaplanName"
                  :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth" prop="rateGroup">
            <el-select v-model="addForm.rateGroup" placeholder="">
              <el-option
                  v-for="item in rateGroup"
                  :key="item.value"
                  :label="item.groupName"
                  :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
        </div>
        <div class="width">
            <el-form-item label="" :label-width="formLabelWidth" prop="expire">
            <el-input
                style="display: inline-block; width: 40%; margin-right: 10px"
                v-model="addForm.expire"
            ></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth" prop="type">
            <el-select v-model="addForm.type" placeholder="">
              <el-option
                  v-for="item in typeList"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </div>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"></el-button>
        <el-button type="primary" @click="submitForm('addForm')"
        > 
        </el-button
        >
      </div>
    </el-dialog>
    <el-dialog title="" :visible.sync="showBar" width="30%" center>
      <p class="title">
        <b></b>
        <span
        > {{ rechargeInfo.directoryNumber }}</span
        >
      </p>
      <el-input
          v-model="chongZhi.balance"
          placeholder=""
      ></el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="showBar = false"> </el-button>
        <el-button type="primary" @click="submit(rechargeInfo)"
        > </el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
import eTree from '../../../components/eTree.vue'
import {getOrganizeId, getOrganizeList} from "@/newwork/system-colltroner";
import {diaPlanList, getRateList} from "@/newwork/ground-colltroner";
import {fn} from "@/uti";
import {addDirectory, deleteDirectory, getDirectory, recharge, upDateDirectory} from "@/newwork/directory";
import myEmpty from "@/newwork/myEmpty";

export default {
  name: 'phoneConfig',
  components: {
    eTree,
    myEmpty
  },
  computed: {
    defaultProps() {
      return {
        children: 'children',
        label: 'deptName',
        value: 'deptId',
        expandTrigger: 'hover'
      }
    },
    getterDeptIdList(){
      return fn(this.deptIdList)
    },
  },
  data() {
    return {
      title: '',
      dialogFormVisible: false,
      formLabelWidth: '150px',
      showBar: false,
      form: {
        name: '',
        SIP: '',
        type: '',
        stats: '',
      },
      deptId: '',//ID
      diaPlanList: [],//
      rechargeInfo: {
        value: null
      },
      chongZhi: {},
      addForm: {
        // domain: '', //
        accountUser: '', //
        balance: '', // balance
        billingType: '', //12
        bussiness: '', //
        deptId: '', // ID
        diaplan: '', //
        directoryName: '', //
        // stats: '', //
        directoryNumber: '', //
        disable: '', //01disable
        // domain: '', //
        endpointId: '45', //ID
        expire: '', //
        latitude: '',  //
        longitude: '', //
        password: '', //
        rateGroup: '', //
        type: '', //:01:23:456:7:8:
      },
      list: [],
      deptIdList:[], //
      rateGroup: [],//
      typeList: [
        {label: '', value: 0},
        {label: '', value: 1},
        {label: '', value: 2},
        {label: '', value: 3},
        {label: '', value: 4},
        {label: '', value: 5},
        {label: '', value: 6},
        {label: '', value: 7},
        {label: '', value: 8},
      ],
      billingType: [
        {label: '', value: 1},
        {label: '', value: 2},
      ],
      relayType: [
        {label: '', value: ''},
        {label: '', value: ''},
      ],
      rules: {
        accountUser: [{required: true, message: ',', trigger: 'blur'},],
        directoryName: [{required: true, message: ',', trigger: 'blur'},],
        billingType: [{required: true, message: ',', trigger: 'blur'},],
        bussiness: [{required: true, message: ',', trigger: 'blur'},],
        directoryNumber: [{required: true, message: ',', trigger: 'blur'},],
        password: [{required: true, message: ',', trigger: 'blur'},],
        balance: [{required: true, message: ',', trigger: 'blur'},],
        domain: [{required: true, message: ',', trigger: 'blur'},],
        disable: [{required: true, message: ',', trigger: 'blur'},],
        deptId: [{required: true, message: ',', trigger: 'blur'},],
        diaplan: [{required: true, message: ',', trigger: 'blur'},],
        rateGroup: [{required: true, message: ',', trigger: 'blur'},],
        expire: [{required: true, message: ',', trigger: 'blur'},],
        type: [{required: true, message: ',', trigger: 'blur'},],
        latitude: [{required: true, message: ',', trigger: 'blur'},],
        longitude: [{required: true, message: ',', trigger: 'blur'},],
      },
      bussiness: [
        {label: '', value: 1},
        {label: '', value: 2},
        {label: '', value: 3},
      ],
      disable: [
        {label: '', value: 0},
        {label: 'disable', value: 1},
      ]
    }
  },
  mounted() {
    this.$bus.$on('pageChange', () => {
      this.form = this.$store.state.formPage
      this.getDirectory(this.form)
    })
  },
  methods: {
    submitForm(addForm) {
      this.$refs['addForm'].validate((valid) => {
        if (valid) {
          if(this.title !== ''){
            addDirectory(this.addForm).then(res => {
              console.log(res)
              if(res.data.code === 200){
                this.$message.success('')
                this.getDirectory(this.form)
                this.dialogFormVisible = false
              }else {
                this.$message.error(res.data.msg)
              }
            }).catch(e => this.$message.error(e))
          }else {
            upDateDirectory(this.addForm).then(res => {
              if(res.data.code === 200){
                this.getDirectory(this.form)
                this.$message.success('')
                this.dialogFormVisible = false
              }else {
                this.$message.error(res.data.msg)
              }
            })
          }
        } else {
          this.$message.error(' ')
          return false
        }
      })
    },
    addForms(row, title) {
      this.dialogFormVisible = true
      this.title = title
      if (title === '') {
        this.addForm = row
      } else {
        this.resetForm('addForm')
        console.log('aaa')
      }
    },
    tableRowClassName({row, rowIndex}) {
      row.index = rowIndex
    },
    removeIt(row) {
      deleteDirectory(row.id).then(res => {
          if(res.data.code === 200){
            this.$message.success('')
            this.getDirectory(this.form)
          }else {
            this.$message.error(res.data.msg)
          }
      }).catch(e => this.$message.error(e))
    },
    resetForm() {
      this.$refs['addForm'].resetFields()
    },
    recharge(row) {
      this.showBar = true
      this.rechargeInfo = row
      this.chongZhi.id = row.id
    },
    submit() {
      recharge(this.chongZhi).then(res => {
        if(res.data.code === 200){
          this.getDirectory(this.form)
          this.$message.success('')
        }else {
          this.$message.error(res.data.data)
        }
      }).catch(e => this.$message.error(e))
      this.showBar = false
    },
    getDiaPlanList(form){
      diaPlanList(form).then(res => {
        console.log(res)
        if(res.data.code === 200){
          this.list = res.data.data.records
        }else {this.$message.error(res.data.msg)}
      }).catch(e => this.$message.error(e))
    },
    getDirectory(form){
      getDirectory(form).then(res => {
        console.log(res)
        this.$store.dispatch('total', res.data.data.total)
        if(res.data.code === 200){
          this.list = res.data.data.records
        }
      }).catch(e => this.$message.error(e))
    },

  },

  created() {
    this.form = this.$store.state.formPage
    this.getDirectory(this.form)
  },
  watch: {
    dialogFormVisible(val){
      if(val){
        getOrganizeList().then(res => {
          this.deptIdList = res.data.data
        }).catch(e => this.$message.error(e))

        /**
         *
         * 
         */
        diaPlanList().then(res => {
          console.log(res)
          this.diaPlanList = res.data.data.records
        }).catch(e => this.$message.error(e))
      }
      /**
       *
       * 
       */
      getRateList().then(res => {
        this.rateGroup = res.data.data.records
      }).catch(e => this.$message.error(e))
    },
    deptId(val){
      this.addForm.deptId = val[ val.length - 1]
    }
  }
}
</script>

