<template>
  <div id="rate">
    <div class="rate-true" v-if="toggle">
      <p>{{ this.$route.meta.title }}</p>
      <div class="container">
        <el-form :inline="true" :model="form" class="demo-form-inline">
          <el-form-item label="">
            <el-input v-model="form.user" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onSubmit"></el-button>
            <el-button @click="clearSubmit"></el-button>
          </el-form-item>
        </el-form>
        <el-button type="primary" @click="showAddForm(null, '')"
        >
        </el-button
        >
      </div>

      <el-dialog :title="title" :visible.sync="dialogFormVisible">
        <el-form :model="addForm" :rules="addRules" ref="addForm">
          <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="groupName"
          >
            <el-input v-model="addForm.groupName" autocomplete="off"></el-input>
          </el-form-item>

          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="addForm.remark" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false"> </el-button>
          <el-button type="primary" @click="submitForm"> </el-button>
        </div>
      </el-dialog>

      <el-table :data="list" stripe style="width: 100%" border v-if="list.length !== 0">
        <el-table-column align="center" prop="date" label="" width="180">
          <template scope="scope">
            {{ scope.$index + 1 }}
          </template>
        </el-table-column>
        <el-table-column align="center" prop="groupName" label="" width="180">
        </el-table-column>
        <el-table-column align="center" prop="address" label="">
          <template scope="scope">
            <el-link
                class="a-link"
                type="info"
                @click="showAddForm(scope.row, '')"
            >
            </el-link
            >
            <el-link class="a-link" type="info" @click="removeIt(scope.row)"
            >
            </el-link
            >
            <el-link class="a-link" type="info" @click="isToggle(scope.row)"
            >
            </el-link
            >
          </template>
        </el-table-column>
        <el-table-column align="center" prop="remark" label=""></el-table-column>
      </el-table>
      <el-empty v-else>
      </el-empty>

    </div>

    <!--    -->
    <!--      toggle == false    -->
    <div class="rate-list" v-else>
      <p></p>
      <div class="container">
        <div class="nav-form">
          <el-form :inline="true" :model="listFrom" class="demo-form-inline">
            <el-form-item label="">
              <el-input
                  v-model="listFrom.numberPrefix"
                  placeholder=""
              ></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input
                  v-model="listFrom.billingName"
                  placeholder=""
              ></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-select v-model="listFrom.region">
                <el-option
                    :label="item.label"
                    :value="item.value"
                    v-for="item in region"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="onSubmit"></el-button>
              <el-button @click="clearSubmit"></el-button>
            </el-form-item>
          </el-form>
          <div>
            <el-button type="primary" @click="onSubmits"></el-button>
            <el-button type="primary" @click="black"></el-button></div>
        </div>
      </div>

      <el-dialog :title="listTitle" :visible.sync="listDialogFormVisible">
        <el-form :model="addListFrom" ref="formName" :rules="addRules">
          <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="rateName"
          >
            <el-input v-model="addListFrom.rateName" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item
              label=""
              :label-width="formLabelWidth"
              name="rateGroup"
              required
          >
            <el-select
                v-model="addListFrom.rateGroupId"
            >
              <el-option
                  :label="item.groupName"
                  :value="item.id"
                  v-for="item in list"
              ></el-option>
            </el-select>
          </el-form-item>
          <div class="width">
            <el-form-item
                label=""
                :label-width="formLabelWidth"
                prop="ratePrefix"
            >
              <el-input
                  class="input"
                  v-model="addListFrom.ratePrefix"
                  autocomplete="off"
              ></el-input>
            </el-form-item>
          </div>
          <el-form-item
              label=""
              :label-width="formLabelWidth"
              required
          >
            <div class="width">
              <el-form-item prop="rate"> <el-input style="margin-right: 20px" v-model="addListFrom.rate" placeholder=""></el-input></el-form-item>

              <el-form-item style="margin-left: 20px" prop="billingPeriod"><el-input v-model="addListFrom.billingPeriod" placeholder=""></el-input></el-form-item>
            </div>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="listDialogFormVisible = false"> </el-button>
          <el-button type="primary" @click="submitForms('')"
          > 
          </el-button
          >
        </div>
      </el-dialog>
      <el-table :data="rateItemList" border style="width: 100%;">
        <el-table-column align="center" prop="date" label="">
          <template scope="scope">{{ scope.$index + 1}}</template>
        </el-table-column>
        <el-table-column align="center" prop="rateName" label="">
        </el-table-column>
        <el-table-column align="center" prop="rate" label="">
        </el-table-column>
        <el-table-column align="center" prop="billingPeriod" label="">
        </el-table-column>
        <el-table-column align="center" prop="ratePrefix" label="">
        </el-table-column>

        <el-table-column align="center" prop="createTime" label="">
        </el-table-column>
        <el-table-column prop="area" label="">
          <template scope="scope">
            <el-link class="a-link" type="info" @click="upDataForm(scope.row)"></el-link>
            <el-link class="a-link" type="info" @click="removeIt(scope.row)"></el-link>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
          class='pagination'
          background
          layout="prev, pager, next"
          :total="1000">
      </el-pagination>
    </div>
  </div>
</template>

<script>

import {
  addRateItemList,
  addRateList,
  deleteRate, deleteRateItem,
  getRateItemList,
  getRateList, putRateList, upDaterateItem
} from "@/newwork/ground-colltroner";

export default {
  name: 'rate',
  data() {
    return {
      title: '',
      form: {
        rateGroupId: '',
      },
      /**
       *
       * 
       */
      addForm: {
        groupName: '',  //
      },
      list: [],  //
      rateItemList: [], //
      dialogFormVisible: false,
      formLabelWidth: '120px',
      toggle: true,
      shows: '',  //
      /**
       * 
       */
      addList: [],
      listFrom: {
        numberPrefix: '',
        billingName: ''
      },
      listTitle: '',
      listDialogFormVisible: false,
      addListFrom: {
        rateName: '',  //
        billingPeriod: '',//
        rateGroupId: '', //ID
        ratePrefix: '', //
        rate: '', //
      },

      region: [
        {label: '', value: ''},
        {label: '', value: ''},
        {label: '', value: ''}
      ],

      addRules: {
        rateName: [
          {required: true, message: '', trigger: 'blur'}
        ],
        ratePrefix: [
          {required: true, message: '', trigger: 'blur'}
        ],
        rate: [
          {required: true, message: '', trigger: 'blur'}
        ],
        billingPeriod: [
          {required: true, message: '', trigger: 'blur'}
        ],

      },
      billingData: [
        {},
        {unit: '30', cost: '0.05', count: 1, priority: 2},
        {unit: '60', cost: '0.08', count: 100, priority: 3}
      ]
    }
  },
  methods: {
    onSubmit() {
    },
    black(){
      this.toggle = true
    },
    clearSubmit() {
      this.listFrom = {}
    },
    getRateList(form) {
      getRateList(form).then(res => {
        this.list = res.data.data.records
      }).catch(e => {
        this.$message.error(e)
      })
    },

    /**
     *
     * 
     * @param form
     */
    getRateItemList(form){
      getRateItemList(form).then(res => {
        console.log(res)
        if(res.data.code === 200){
          this.rateItemList = res.data.data.records
        }else {
          console.log(res)
        }
      }).catch(e => {
        this.$message.error(e)
      })
    },
    onSubmits() {
      this.listDialogFormVisible = true
    },
    isToggle(row) {
      this.toggle = false

      this.form.rateGroupId =row.id
          this.getRateItemList(this.form)
    },
    /**
     *
     * 
     */
    submitForm() {
      this.$refs.addForm.validate((valid) => {
        if (valid) {

          if(this.title === ''){
            addRateList(this.addForm).then(res => {
              console.log(res)
            })
            this.getRateList(this.form)
            this.dialogFormVisible = false
          }else{
            putRateList(this.addForm).then(res => {
              console.log(res)
              if(res.data.code === 200){
                this.$message.success('')
                this.dialogFormVisible = false
              }else {
                this.$message.error(res.data.msg)
              }
            })
          }
        }
      })
    },
    //
    submitForms() {
      this.$refs.formName.validate((valid) => {
        if (valid) {
          if(this.listTitle === ''){
            addRateItemList(this.addListFrom).then(res => {
              console.log(res)
              if(res.data.code === 200){
                this.$message.success('')
              }else {
                this.$message.error(res.data.msg())
              }
            }).catch(e => {
              this.$message.error(e)
            })
          }else {
            upDaterateItem(this.addListFrom).then(res => {
              console.log(res)
              if(res.data.code === 200){
                this.getRateItemList(this.form)
                this.listDialogFormVisible = false
                this.$message.success('')

              }else {
                this.$message.error(res.data.msg)
              }
            })
          }
        } else {
          this.$message({
            message: ' ',
            type: 'error'
          })
          return false
        }
      })
    },
    showAddForm(row, title) {
      this.title = title
      if(title === ''){
        console.log(row)
        this.addForm = row
      }
      this.dialogFormVisible = true
    },
    //

    upDataForm(row){
      this.addListFrom = row
      this.listTitle = ''
      this.listDialogFormVisible = true
    },
    //
    removeIt(row) {
      this.toggle ? deleteRate(row.id).then(res=> {
        if(res.data.code == 200){
          this.$message.success('')
          this.getRateList(this.form)
        }else {
          this.$message.error(res.data.msg)
        }
      }).catch(e =>   this.$message.error(e)) : deleteRateItem(row.id).then(res => {
        if(res.data.code ===200){

          this.$message.success('')
          //formid ok
          this.getRateItemList(this.form)
        }else {
          this.$message.error(res.data.msg)
        }
      })
    },

    //  
    resetForm() {
      this.$refs.formName.resetFields();
    }
  },
  watch: {
    listDialogFormVisible(val){
      if(val!== true){
        this.resetForm()
      }
    },

  },
  computed: {
    createId() {
      return (this.addForm.id = (Math.random() * 1000000).toFixed(0))
    }
  },
  created() {
    this.getRateList(this.form)
    this.getRateItemList(this.form)
  }
}
</script>

<style scoped>
#rate > div[class] > p {
  padding: 10px 15px;
  border-radius: 5px;
  background-color: #f2f2f2;
}

.container {
  display: flex;
  justify-content: space-between;
  margin: 20px 0;
  height: 40px;
  background-color: #fff;
}

.a-link {
  margin-right: 20px;
}

.nav-form {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.pagination{
  margin-top: 20px;
}
.width {
  display: flex;
}

.width .input {
  width: 193px;
}
</style>
