<script>
import {deletePbx, getPbxAdd, getPbxAll, upDatePbx} from "@/newwork/ground-colltroner";

export default {
  name: 'networkManagement',
  data() {
    return {
      dialogFormVisible: false,
      formLabelWidth: '120px',
      title: '',
      from: {
        user: '',
        address: '',
        protocol: '',
        relay: '',
        recording: '',
        networkStat: '',
        pageNum: 1,
        pageSize: 10,
      },
      list: [],
      addFrom: {
        accountUser: '', //
        codecs: '', //
        dtmfMode: '', //dtmfMode
        expires: '', //
        gatewayIp: '', //IP
        gatewayName: '', //
        gatewayPort: '', //
        gatewayType: '', //
        maxCall: '', //
        password: '', //
        profileId: '', //profileId
        proxyIp: '', //IP
        realm: '', //
        register: 1, // 0/ 1 
        username: '', //


      },

      protocolType: [
        {label: 'SIP', value: 'SIP'},
        {label: 'H.323', value: 'H.323'}
      ],
      relayType: [
        {label: '', value: '0'},
        {label: '', value: '1'},
        {label: '', value: '2'}
      ],
      networkStat: [
        {label: '', value: ''},
        {label: '', value: ''}
      ],
      recording: [
        {label: '', value: ''},
        {label: '', value: ''}
      ],
      rules: {
        accountUser: [{required: true, message: ' ', trigger: 'blur'}],
        codecs: [
          {required: true, message: ' ', trigger: 'blur'}
        ],
        dtmfMode: [
          {required: true, message: ' ', trigger: 'blur'}
        ],
        expires: [{required: true, message: ' ', trigger: 'blur'}],
        gatewayIp: [
          {required: true, message: ' ', trigger: 'blur'}
        ],
        gatewayName: [
          {required: true, message: ' ', trigger: 'blur'}
        ],
        gatewayPort: [{required: true, message: ' ', trigger: 'blur'}],
        gatewayType: [
          {required: true, message: ' ', trigger: 'blur'}
        ],
        maxCall: [{required: false, message: ' ', trigger: 'blur'}],
        password: [
          {required: true, message: ' ', trigger: 'blur'}
        ],
        profileId: [
          {required: true, message: ' ', trigger: 'blur'}
        ],
        proxyIp: [{required: true, message: ' ', trigger: 'blur'}],
        realm: [{required: true, message: ' ', trigger: 'blur'}],
        register: [{required: true, message: ' ', trigger: 'blur'}],
        username: [{required: true, message: ' ', trigger: 'blur'}],
      }
    }
  },
  methods: {
    getPbxAll(form) {
      getPbxAll(form).then(res => {
        this.list = res.data.data.records
      }).catch(e => {
        this.$message.error(e)
      })
    },
    submitForm() {
      this.$refs['formName'].validate((valid) => {
        if (valid) {
          if (this.title === '') {
            getPbxAdd(this.addFrom).then(res => {
              console.log(res)
              if (res.data.code === 200) {
                this.$message.success('')
                this.getPbxAll(this.from)
                this.dialogFormVisible = false
              } else {
                this.$message.error(res.data.msg)
              }
            })
          } else {
            console.log(this.addFrom)
            upDatePbx(this.addFrom).then(res => {
              if (res.data.code === 200) {
                this.$message.success('')
                this.getPbxAll(this.from)
                this.dialogFormVisible = false
              } else {
                this.$message.error(res.data.msg)
              }
            })
          }
          // this.resetForm('formName')
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    resetForm(formName) {
      this.$refs['formName'].resetFields();
    },
    showAddForm(row, title) {
      this.dialogFormVisible = true
      this.title = title
      if (title === '') {
        this.addFrom = row
      } else if (title === '') {
        this.addForm = row
        this.isReadOnly = true
      } else {
        // this.resetForm("formName")
      }
    },
    tableRowClassName({row, rowIndex}) {
      row.index = rowIndex
    },

    removeIt(row) {
      console.log(row)
      deletePbx(row.id).then(res => {
        console.log(res)
        if (res.data.code === 200) {
          this.$message.success('')
          this.getPbxAll(this.form)
        } else {
          this.$message.error(res.data.msg)
        }
      }).catch(e => {
        this.$message.error(e)
      })
    },
  },
  created() {
    this.getPbxAll(this.form)
  },
  watch: {
    dialogFormVisible(val) {
      if (val === false) {
        console.log('--------')
        this.resetForm()
      }
    }
  }

}
</script>

