<template>
  <div id="trunk">
    <div class="container">
      <p></p>
    </div>
    <div class="nav-form">
      <el-form :inline="true" :model="from" class="demo-form-inline">
        <el-form-item label="">
          <el-input v-model="from.user" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="from.protocol" placeholder="">
            <el-option
                :label="item.label"
                :value="item.value"
                v-for="item in policyType"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="from.relay" placeholder="">
            <el-option
                :label="item.label"
                :value="item.value"
                v-for="item in retryStrategyType"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <!--          <el-button type="primary" @click="onSubmit"></el-button>-->
          <!--          <el-button @click="onSubmit"></el-button>-->
        </el-form-item>
      </el-form>
      <el-button type="primary" @click="showAddForm(null, '')"
      >
      </el-button
      >
    </div>

    <el-dialog :title="title" :visible.sync="dialogFormVisible">
      <el-form :model="addFrom" ref="addForm" :rules="rules">
        <div class="width">
          <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="groupName"
          >
            <el-input v-model="addFrom.groupName" autocomplete="off"></el-input>
          </el-form-item>
        </div>
        <div class="width">
          <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="strategyRetry"
          >
            <el-select v-model="addFrom.strategyRetry" placeholder="">
              <el-option
                  v-for="item in retryStrategyType"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="strategyType"
          >
            <el-select v-model="addFrom.strategyType" placeholder="">
              <el-option
                  v-for="item in policyType"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
        </div>

        <el-form-item style="margin-top: 20px" label=""  :label-width="formLabelWidth">
          <ul>
            <li v-for="(item, i) in tableData">
              <el-select v-model="tableData[i].gatewayId">
                <el-option
                    v-for="item in pbxList"
                    :key="item.value"
                    :label="item.gatewayName"
                    placeholder=""
                    :value="item.id">
                </el-option>
              </el-select>
              <el-input class="li-input" v-model="tableData[i].weight" placeholder=""></el-input>
            </li>
          </ul>
        </el-form-item>
        <el-form-item
            label=""
            :label-width="formLabelWidth"
        >
          <el-input
              v-model="addFrom.remark"
              placeholder=""
          ></el-input>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="submitForm()"
        > 
        </el-button
        >
      </div>
    </el-dialog>

    <el-table border :data="list" style="width: 80%">
      <el-table-column align="center" prop="index" label="" width="50">
        <template scope="scope">{{ scope.$index + 1 }}</template>
      </el-table-column>
      <el-table-column align="center" prop="groupName" label="">
        <template scope="scope">
          {{ scope.row.groupName }}
        </template>
      </el-table-column>
      <el-table-column align="center" prop="pbxGwgroupGatewayList" label="">
        <template scope="scope">
          <span v-if="scope.strategyType === 0"></span>
          <span v-else></span>
        </template>
      </el-table-column>
      <el-table-column align="center" prop="pbxGwgroupGatewayList" label="">
        <template scope="scope">
          <span v-if="scope.strategyRetry === 0"></span>
          <span v-else></span>
        </template>
      </el-table-column>
      <el-table-column align="center" prop="retryStrategy" label="" width="300px">
        <template scope="scope">
          <div class="table" v-for="(item, i) in scope.row.pbxGatewayList">
            <p>
              <span>{{ scope.row.pbxGatewayList[i].gatewayName }}</span> --<span>{{ scope.row.pbxGwgroupGatewayList[i].createTime }}</span>
            </p>
          </div>
        </template>
      </el-table-column>
      <el-table-column align="center" prop="remark" label="">
        <template scope="scope">
          <el-link
              style="margin-right: 20px"
              @click="showAddForm(scope.row, '')"
              type="info"
          >
          </el-link
          >
          <el-link @click="removeIt(scope.row)" style="margin-right: 20px" type="info"></el-link>
        </template>
      </el-table-column>
      <el-table-column align="center" prop="remark" label="">
        <template scope="scope">
          {{ scope.row.remark || '' }}
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import {addGwgroup, deleteGwgroup, getGwgroup,  getPbxAll} from "@/newwork/ground-colltroner";

export default {
  name: 'trunkGroup',
  data() {
    return {
      from: {},
      title: '',
      dialogFormVisible: false,
      pbxList: [],
      addFrom: {
        groupName: '', //
        strategyRetry: '', //
        strategyType: '', // 
        pbxGwgroupGatewayList: '', //
        totalWeight: 0, //
        pageNum: 1,
        pageSize: 10,
      },
      list: [],
      startTime: '',
      formLabelWidth: '120px',
      policyType: [
        {label: '', value: 0},
        {label: '', value: 1}
      ],
      retryStrategyType: [
        {label: '', value: 0},
        {label: '', value: 1}
      ],
      rules: {
        groupName: [
          {required: true, message: '', trigger: 'blur'}
        ],
        strategyRetry: [
          {required: true, message: '', trigger: 'blur'}
        ],
        strategyType: [
          {required: true, message: '', trigger: 'blur'}
        ],
        retryStrategy: [
          {required: true, message: '', trigger: 'blur'}
        ],
        startTime: [{required: true, message: '', trigger: 'blur'}]
      },
      value: '',
      tableData: [{gatewayId: '', weight: ''},{gatewayId: '', weight: ''}]
    }
  },
  methods: {
    getGwgroup(form){
      getGwgroup(form).then(res => {
        this.list = res.data.data.records
      }).catch(e => {
        this.$message.error(e)
      })
    },
    tableRowClassName({row, rowIndex}) {
      row.index = rowIndex
    },

    showAddForm(row, title) {
      this.dialogFormVisible = true
      this.title = title
      if (title === '') {
        this.addFrom = row
      }else {
       this.addFrom = {
         groupName: '', //
         strategyRetry: '', //
         strategyType: '', // 
         pbxGwgroupGatewayList: '', //
         totalWeight: '', //
         pageNum: 1,
         pageSize: 10,
       }
      }
    },
    submitForm() {
      this.$refs['addForm'].validate((valid) => {
        if (valid) {
          const totalWeight = []
          this.tableData.forEach(item => {
            if(item.gatewayId.length !== 0 && item.weight !== 0){
              totalWeight.push(parseInt(item.weight))
              this.dialogFormVisible = false
            }else {
              this.$message.error('')
            }
          })
          let s = 0
          totalWeight.forEach((item, i) => {
            s += parseInt(item)
            this.addFrom.totalWeight = s
          })
          console.log(this.addFrom.totalWeight )
          if(this.addFrom.totalWeight.length !== 0){
            //
            addGwgroup(this.addFrom).then(res => {
              if(res.data.code === 200){
                this.$message.success('')
                this.getGwgroup(this.form)
              }else {
                this.$message.error(res.data.msg)
              }
            }).catch(e => {
              this.$message.error(e)
            })
          }
        } else {
          this.$message.error(' ')
          return false
        }
      })
    },
    resetForm() {
      this.$refs['addForm'].resetFields()
    },
    removeIt(row) {
      deleteGwgroup(row.id).then(res => {
        console.log(row)
        console.log(res)
        if(res.data.code === 200){
          this.$message.success('')
          this.getGwgroup(this.from)
        }else {
          this.$message.error(res.data.msg)
        }
      }).catch(e => {
        this.$message.error(e)
      })
    },

  },

  created() {
    this.getGwgroup(this.form)
  },
  watch: {
    dialogFormVisible(val ){
      if(val){
        console.log(val)
        getPbxAll().then(res => {
          this.pbxList = res.data.data.records
        })
      }
    },
    tableData: {
      handler: function (val){
        this.addFrom.pbxGwgroupGatewayList = val
      },
      deep: true
    },
    totalWeight(val){
      if(val.length !== 0){
      }
    }
  }
}
</script>

